.sidebarOption {
    display: flex;
    cursor: pointer;
    align-items: center;
    height: 40px;
    color: grey;
    transition: 200ms color ease-in;
}

.sidebarOption:hover {
    color: white;
}

.sidebar>hr {
    border: 0.5px solid #030101;
    width: 90%;
    outline: none;
    margin: 10px auto;
}

.sidebarOption_icon {
    padding-left: 10px;
    padding-right: 10px;
}

.sidebarOption>p {
    margin-top: 10px;
    margin-left: 20px;
    font-size: 14px;
}